import styled from "styled-components";
import Description from "./descriptionMobile";
import PhoneMobile from "./phoneMobile";

const StyledIntro = styled.section`
  display: flex;
  background-color: #000f67;
  margin-top: -80px;
  padding-top: 120px;
  padding-bottom: 50px;
  overflow-x: hidden;
  position: relative;

  z-index: -1;

  background: radial-gradient(
      91.03% 33.38% at 27.82% 24.15%,
      #ff7cda 0%,
      rgba(255, 124, 218, 0) 100%
    ),
    radial-gradient(
      111.79% 29.01% at 59.62% 52.18%,
      #00aee8 0%,
      rgba(0, 174, 232, 0) 100%
    ),
    radial-gradient(
      142.95% 47.86% at 0% 101.12%,
      #7400e8 0%,
      rgba(116, 0, 232, 0) 100%
    ),
    #000f67;

  & * {
    z-index: 100;
  }
`;

const IntroInner = styled.div`
  margin: 0 16px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  gap: 50px;
`;

const IntroMobile = ({ language }) => {
  return (
    <StyledIntro id={"intro"}>
      <IntroInner>
        <Description language={language} />
        <PhoneMobile />
      </IntroInner>
    </StyledIntro>
  );
};

export default IntroMobile;
