import { Phone_Svg } from "./PhoneIntro";
import styled from "styled-components";

import VideoBtn from "../../assets/images/Elements/VideoBtn.svg";

const IntroPhone = styled.div`
  width: 100%;
  max-width: 386px;
  height: fit-content;
  margin: 0 auto;
  position: relative;

  & img {
    aspect-ratio: 1 / 1;
  }
`;

const ButtonVideo = styled.a`
  position: absolute;
  left: 0px;
  bottom: 0px;
  width: 136px;
  height: 136px;
  border-radius: 200px;

  background: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;

  user-select: none;

  z-index: 1000;

  & img {
    width: 125px;
    height: 125px;
    aspect-ratio: 1/1;
  }
`;

const PhoneSvg = styled.div`
  display: flex;
  width: 100%;
  margin: auto;

  & svg {
    margin: 0 auto;
    width: 326px;
    height: 377px;
    margin-right: -10px;
  }
`;

const PhoneMobile = () => {
  return (
    <IntroPhone>
      <ButtonVideo>
        <img alt="Посмотреть видео о сервисе" src={VideoBtn} />
      </ButtonVideo>
      <PhoneSvg>{Phone_Svg}</PhoneSvg>
    </IntroPhone>
  );
};

export default PhoneMobile;
